import React from "react"
import { Link } from "gatsby"
import { FormattedMessage, injectIntl } from "gatsby-plugin-intl"
import { Jumbotron } from "react-bootstrap"
import Layout from "@components/layout"
import SEO from "@components/helper/seo"
import ErrorImg from '@assets/404.gif'

const ErrorPage = ({ intl }) => {
    return (
      <Layout>
        <SEO
          lang={intl.locale}
          title={`404: ${intl.formatMessage({ id: "title" })}`}
        />
        <Jumbotron className="text-center">
          <img className="img-fluid mb-4" src={ErrorImg} alt="Error 404" />
          <h1><FormattedMessage id="page.notfound.title" /></h1>
          <p className="lead"><FormattedMessage id="page.notfound.description" /></p>
          <Link to="/"><FormattedMessage id="action.goBack" /></Link>
        </Jumbotron>
      </Layout>
    )
  }
export default injectIntl(ErrorPage)
